import Header from "@/components/common/Header";
import { verifyGoodsDetalis } from "@/servers/api";
import { timeTodate } from "@/assets/js/tools";

export default {
  name: "",
  props: [""],
  data() {
    return {
      title: "文链信息",
      TitleObjData: {
        titleContent: "文链信息",
        showLeftBack: true,
        showRightMore: false
      },
      blockChainList: {
        name: "",
        wc: "",
        yc: {},
        sy: "",
        jy: "",
        sm: "",
        rh: ""
      },
      goodInfo: {}
    };
  },

  components: {
    Header
  },
  filters: {
    formTime(val) {
      return val ? timeTodate(val) : "";
    }
  },
  computed: {
    scenceConsumHeight() {
      return { height: document.documentElement.clientHeight - 45 + "px" };
    }
  },

  beforeMount() {},
  beforeCreate() {},
  beforeMount() {
    this.initData();
  },
  mounted() {},

  methods: {
    // 查看详情
    getChainInfoFn(title, content) {
      content.rule = this.$route.query.rule;
      let con = JSON.stringify(content);
      this.$store.commit("setBlockChainInfo", con);
      this.$router.push("/blockchaininfodetails?title=" + title);
    },
    // 初始化
    initData() {
      let dataArr = [];
      let dataArrData = {
        name: "",
        wc: "",
        yc: "",
        sy: "",
        jy: "",
        sm: "",
        rh: ""
      };
      verifyGoodsDetalis({
        goods_id: this.$route.query.id
      })
        .then(res => {
          if (res.result === 1) {
            this.goodInfo = res.data.goods;
            if (res.data.blockCurlData.data) {
              for (
                let i = 0;
                i < res.data.blockCurlData.data.rows.length;
                i++
              ) {
                let flag = res.data.blockCurlData.data.rows[i].strData.indexOf(
                  "{"
                );
                if (flag !== -1) {
                  dataArr.push({
                    owner: res.data.blockCurlData.data.rows[i].owner,
                    strData: JSON.parse(
                      res.data.blockCurlData.data.rows[i].strData
                    )
                      ? JSON.parse(res.data.blockCurlData.data.rows[i].strData)
                      : "",
                    strType: res.data.blockCurlData.data.rows[i].strType
                  });
                }
              }
              for (let i = 0; i < dataArr.length; i++) {
                if (dataArr[i].strType === "yc") {
                  this.blockChainList.name = dataArr[i].owner;
                  this.blockChainList.wc = dataArr[i].strData.writing_order;
                  this.blockChainList.yc = dataArr[i].strData;
                } else if (dataArr[i].strType === "sy") {
                  this.blockChainList.sy = dataArr[i].strData;
                } else if (dataArr[i].strType === "jy") {
                  this.blockChainList.jy = dataArr[i].strData;
                } else if (dataArr[i].strType === "sm") {
                  this.blockChainList.sm = dataArr[i].strData;
                } else if (dataArr[i].strType === "rh") {
                  this.blockChainList.rh = dataArr[i].strData;
                }
              }
            }
          }
        })
        .catch(err => {
          console.log(err);
        });
    }
  },
  beforeDestroy() {},

  watch: {}
};



